<script type="text/javascript">
	export default{
		name : 'Index',
		mounted(){
			$("#alert_coa").hide();
			var titles = {
				"#finance" : "Finance",
				"#marketing" : "Marketing",
				"#operation" : "Operation",
				"#bod" : "Board of Director",
			};
			$(".table").DataTable({
				"scrollY": "43vh",
				"scrollX": true,
			});
			$(".nav-tab-link .nav-link").on("click", function(){
				$("#table").hide();
				$("#alert_coa").hide();
				var target = $(this).attr('href');
				if(target != '#vacant'){
					$("#table").show(500);
					$("#coa_title").html(titles[target]+" COA");
				}else{
					$("#alert_coa").show(500);
				}
			})
		}
	}
</script>
